body{
  background-color: #65658F;
  color: whitesmoke;
}

h1{
  color: #120A19;
  text-align: center;
  margin-top: 5%;
  font-family:'Times New Roman', Times, serif;
  font-weight: bold;
  size: 10em;
  font-size: 3em;

}

.container{

  display: block;
  margin-left: 38%;
  margin-top: 3%;
  width: 30%;
  max-width: 50vh;
  height: auto;
  border: solid 2px #120A19;
  background-color: rgba(84, 41, 124, .9);
  box-shadow: 0 15px 25px rgba(0,0,0,.6);
  border-radius: 10px;
 

 
}

ul{
  list-style-type: none;
}

.todo-input{
  width: 200px;
  margin-top: 25px;
  margin-left: 25px;
  height: 20px;
  
}

.todo-input::placeholder{
  border-color:rgba(84, 41, 124, .9);
}


.button{
  border-color: rgba(84, 41, 124, .9);
  height: 30px;
  background-color: rgba(205, 192, 216, 0.9) ;
}
.button:hover{
background-color:   rgba(234, 135, 243, 0.9);
}

.todo-style{
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
}

li{
  margin-left: 2%;
}

i{
  justify-content:space-evenly;

}

